*{
    margin: 0;
    padding: 0;
    box-sizing:border-box;
}

.main{
    display: flex;
    justify-content: center;
    
    
}


form{
    
    display: grid;
    padding: 3rem;
    margin: 3px;
    background-color: rgb(39, 39, 66);
    color: white;
    height: 50vh;
    border-radius: 20px;
    border: 1px solid blueviolet;
    max-width:fit-content;
    max-height: fit-content;

}

.imageSection{
    display: flex;
    justify-content: flex-start;
}
#txtArea{
    height: 3rem;
    
    
}
#txtArea:hover{
    height: 5rem;
}

#addPht{
    border-radius: 10px;
    width: 9rem;
}

#addPht:hover{
    background-color: rgb(87, 182, 255);
    border: none;
}


